}

.slider:before {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #f8f8f8;
  border: 1px solid #cecdcd;
  border-radius: 50%;
  transition: all .5s ease-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0);
}

.checkbox:checked + .slider {
  background-color: #9bb537;
}

.checkbox:checked + .slider:before {
  transform: translateX(26px);
}

