  display: flex;
}

.ss-main .ss-values .ss-single {
  margin: auto 0px auto var(--ss-spacing-s);
  display: flex;
}

.ss-main .ss-values .ss-value {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: fit-content;
